import './index.scss'
import GoogleAutoComplete from 'react-auto-complete-address-fields';
import { useState } from 'react'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { createProfile } from '../../actions/userActions'
import Summary from '../Summary'

export default function Info({ history }) {
	const dispatch = useDispatch()
	const { user } = useSelector(state => ({
		user: state.currentUser
    }))
	const [address, setAddress] = useState({ subpremise:'', street_number:'', route:'', locality:'', administrative_area_level_1:'', postal_code:'', country:'' })
	const placeholder = ['Apartment, suite, etc. (optional)','Steet Number*','Street Name*','City*','State/Province*','Postal Code*','Country/Region*']
	const handleInput = ({ name, value }) => setAddress(prev=>({...prev, [name]: value}))
    const callbackFunc = autoCompleteData=> {
		autoCompleteData.address_components.forEach(item=>
			handleInput({ name: [item.types[0]], value: item.long_name })
		)
	}
	const handleFormSubmit = e => {
        e.preventDefault()
		const addressString = `${address.subpremise ? address.subpremise + ' - ' : ''}${address.street_number} ${address.route}, ${address.locality}, ${address.administrative_area_level_1}, ${address.postal_code}`
		const capitalizeFirstLetter = ([ first, ...rest ], locale = navigator.language) => first.toLocaleUpperCase(locale) + rest.join('')
		dispatch(createProfile({
			email: e.target['email'].value, 
			first_name: capitalizeFirstLetter(e.target['first_name'].value), 
			last_name: capitalizeFirstLetter(e.target['last_name'].value), 
			address: address, 
			addressString: addressString, 
			phone: e.target['phone'].value 
		}))
        history.push("/shipping")
    }
	
    return (
        <div className='wrapper'>
			 <div className='title'>
                <span>Information</span>
            </div>
			<div className="information">
				<div className="info-panel">
					<span> 
						cart 
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg> 
						<strong>information</strong>
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg>
						shipping
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg>
						payment
					</span>
					<div className="express-checkout">
						<div>
							Express checkout
						</div>
						<button disabled>Apple Pay</button>
						<button disabled>G Pay</button>
						<button disabled>PayPal</button>
					</div>
					<div className="or">
						<span>or</span>
					</div>
					<div className="contact-info-header">
						<span>Contact Information</span>
						<span>Already have an account? <Link to='/sign_in'>Log in</Link></span>
					</div>
					<form onSubmit={handleFormSubmit}>
						<input type='text' name='email' placeholder='Email' disabled={user && user.email} required />
						<div className="shipping-header">
							<span>Shipping address</span>
						</div>
						<div className="shipping-address">
							<GoogleAutoComplete apiKey={process.env.REACT_APP_GOOGLE_KEY} id = 'location' callbackFunction={callbackFunc}
								fields= {{
									subpremise: "subpremise",
									streetAddress: "route",
									locality: "locality",
									cityOrState: "administrative_area_level_1",
									postalcode: "postal_code",
									country: "country"
								}}
							/>
							<input type="text" name="first_name" placeholder="First Name*" required />
							<input type="text" name="last_name" placeholder="Last Name*" required />
							{ Object.keys(address).map((k,i) =>k !== 'administrative_area_level_2' ? 
							<input key={i} type='text' name={k} value={address[k]} placeholder={placeholder[i]} 
							onChange={e=>handleInput(e.currentTarget)} required={k!=='subpremise'} /> : null) }
							<input type="tel" name="phone" placeholder="Phone" />
							<div className="form-submit">
								<input type='submit' className='btn btn-success' value='Continue to shipping' />
								<Link to='/cart'>Return to cart</Link>
							</div>
						</div>
					</form>
				</div>
				<Summary />
			</div>
		</div>
    )
}