import './index.scss'
import { useState, useEffect } from 'react'
import { createPromo, listPromos } from '../../actions/promoActions.js'
import { listNewsletter } from '../../actions/newsletterActions.js'
import { orders } from '../../actions/orderActions.js'

export default function Admin() {
    const [promoMessage, setPromoMessage] = useState('')
    const [promoList, setPromoList] = useState([])
    const [newsletterList, setNewsletterList] = useState([])
    const [allOrders, setAllOrders] = useState([])
    const handlePromoSubmit = async (e)=>{
        e.preventDefault()
        let submitPromo = await createPromo({
            name: e.target.name.value.toUpperCase(),
            percent: e.target.percent.value,
            expire: e.target.expire.value
        })
        submitPromo.length ? setPromoMessage('Created Promo Code') : setPromoMessage('Invalid Promo Info')
        fetchPromos()
    }
    const fetchPromos = async () => {
        let promos = await listPromos()
        if (promos && promos.length) setPromoList(promos)
    }
    const fetchNewsletter = async () => {
        let newsletter = await listNewsletter()
        if (newsletter && newsletter.length) setNewsletterList(newsletter)
    }
    const fetchOrders = async () => {
        let theseOrders = await orders()
        if (theseOrders && theseOrders.length) setAllOrders(theseOrders)
    }

    useEffect(()=>{
        fetchPromos()
        fetchNewsletter()
        fetchOrders()
    },[])
    
    return (
        <div className='wrapper'>
            <div className='title'>
                <span>Admin Control Panel</span>
            </div>

            <h5>Create Promo Code</h5>
            <form onSubmit={handlePromoSubmit}>
                <input type='text' name='name' placeholder='Promo Code Name' required />
                <input type='number' name='percent' placeholder='Discount Percentage' min='0' max='100' required />
                <input type='date' name='expire' min={Date()} required />
                <input type='submit' value='Submit' className='btn btn-secondary btn-sm' />
            </form>
            {promoMessage && <p>{promoMessage}</p>}
            <br></br>
            <h5>List All Promo Codes</h5>
            <table className="table table-striped table-sm">
                <thead>
                    <tr>
                        <th>Promo Code Name</th>
                        <th>Percentage</th>
                        <th>Expires</th>
                    </tr>
                </thead>
                <tbody>
                {promoList.length ? promoList.map((item, i)=>
                    <tr key={i}>
                        <td>{item.promoID}</td>
                        <td>{item.percent}</td>
                        <td>{new Date(item.expire).toString().slice(0,24)}</td>
                    </tr>
                ) : null}          
                </tbody>
            </table>
            <br></br>
            <h5>List All Newsletter</h5>
            <table className="table table-striped table-sm">
                <thead>
                    <tr>
                        <th>Email</th>
                        <th>Subscription</th>
                    </tr>
                </thead>
                <tbody>
                {newsletterList.length ? newsletterList.map((item, i)=>
                    <tr key={i}>
                        <td>{item.emailID}</td>
                        <td>{item.subscribed ? 'Subscribed' : 'Unsubscribed'}</td>
                    </tr>
                ) : null}          
                </tbody>
            </table>
            <br></br>
            <h5>List All Orders</h5>
            <table className="table table-striped table-sm">
                <thead>
                    <tr>
                        <th>OrderID</th>
                        <th>CartID</th>
                    </tr>
                </thead>
                <tbody>
                {allOrders.length ? allOrders.map((item, i)=>
                    <tr key={i}>
                        <td>{item.orderID}</td>
                        <td>{item.cartID}</td>
                    </tr>
                ) : null}          
                </tbody>
            </table>
        </div>
    )
}